import React, {useState} from 'react'

function Contact() {
    const [formName, setName] = useState('')
	const [formEmail, setEmail] = useState('')
	const [formCompany, setCompany] = useState('')
	const [formMessage, setMessage] = useState('')

    return (
        <div id="contact-container">
			<h1 className="pageTitle">Get in touch</h1>

			<p>Interested in contacting me for work or a copy of my resume? </p>
			<p>Please submit an enquiry below!</p>

			<form id="contact-form">
				<label className="form-content">
					Name<text className="form-required">*</text><br/>
					<input type="text" 
						className="form-input"
						onChange={(e) => {
      						setName(e.target.value)}}
						name="formName" 
						value={formName} 
						required/> 
				</label>
				<hr className="form-HR"></hr>

				<label className="form-content">
					Email<text className="form-required">*</text><br/>
					<input type="email" 
						className="form-input" 
						onChange={(e) => {
      						setEmail(e.target.value)}}
						name="formEmail" 
						value={formEmail} 
						required/> 
				</label>
				<hr className="form-HR"></hr>

				<label className="form-content">
					Company<br/>
					<input type="text" 
						className="form-input" 
						onChange={(e) => {
      						setCompany(e.target.value)}}
						name="formCompany" 
						value={formCompany}/> 
				</label>
				<hr className="form-HR"></hr>

				<label className="form-content">
					Message<text className="form-required">*</text><br/>
					<textarea 
						id="form-textarea" 
						name="form-message"
						onChange={(e) => {
      						setMessage(e.target.value)}}
						value={formMessage} 
						required/>
				</label>
				<button id="form-submit">Submit</button>
			</form>
		</div>
    )
}

export default Contact