import React from "react"
import Typewriter from "typewriter-effect"

function Home () {
	
	return (
		<div className="home-container">
			<Typewriter
  				options={{
					strings: '<h1 id="name-title"><text class="variable">const</text> Philip_Sung <text class="plain">= \{</text></h1><br><h2 class="name-object"><text class="name-object-category">Goal:</text> "Front End Web Developer"</h2><br><h2 class="name-object"><text class="name-object-category">Location:</text> "Adelaide, South Australia"</h2><br><h2 class="name-object"><text class="name-object-category">Interests:</text> [ "Cars/Motorbikes", "Gaming", "Gym" ]</h2><br><h1 class="plain">\}<text id="typewriter-cursor">_</text></h1>',
					autoStart: true,
					delay: 20
				}}
			/>
		</div>
		)
}

export default Home